@import '../../variables.scss';

.btn-group {
    margin-top: 20px;
    button{
     border-color: $main-color;
     border-width: 2px;
        &:hover{
            background-color: $main-color;
            color: grey;
            border-color: $main-color;
        }
        &.btn-light{
            border-color: $main-color;
            background-color:$main-color;
            color: gray;
        }
    }
}
