@import '../../variables.scss';

.add-form {
    margin-top: 20px;
    input {
        width: 350px;
        margin-right: 20px;
    }
}

.app-add-form {
    margin-top: 30px;
    padding: 25px;
    background-color: grey;
    border-radius: 4px;
    box-shadow: 15px 15px 30px rgba(0,0,0, .15);
    color: #fff;
    button{
        border-color: $main-color;
        border-width: 2px;
           &:hover{
               background-color: $main-color;
               color: grey;
               border-color: $main-color;
           }
           &.btn-light{
               border-color: $main-color;
               background-color:$main-color;
               color: gray;
           }
       }
}